.FloatingButton {
  height: 50px;
  width: 50px;
  background: #2fb454;
  border-style: solid;
  border-width: 2px;
  color: white;
  border-radius: 50%;
  cursor: pointer;
  line-height: 50px;
  font-size: 25px;
  text-align: center;
  position: absolute;
  top: 0px;
  left: 0px;
  -webkit-transition-duration: 0.5s;
       -o-transition-duration: 0.5s;
          transition-duration: 0.5s;
  outline: none; }

.active > .FloatingButton {
  background: white;
  color: #2fb454; }

.icon {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }



